    height: 40px;
    padding: 8px;
    transition: all .2s;
    display: flex;

    & svg {
      stroke: #a3a3a3;
      width: 24px;
      height: 24px;
      transition: all .2s;
    }

    &:hover {
      background: #6d84341a;
